import { isMarketCheckbox } from '../../market-checkbox/types';
import { isMarketToggle } from '../../market-toggle/types';

export type MarketTableV2ControlElement = HTMLMarketCheckboxElement | HTMLMarketToggleElement;

export function isMarketTableV2ControlElement(value: unknown): value is MarketTableV2ControlElement {
  return isMarketCheckbox(value) || isMarketToggle(value);
}
